import React from 'react';

const GifNotFound = props => (
  <li className='gifs-not-found'>
    <i className="material-icons icon-gif">sentiment_very_dissatisfied</i>
    <h3>Sorry, no Pugs match your search.</h3>
  </li>
);

export default GifNotFound;


